html
    -webkit-box-sizing: border-box
    box-sizing: border-box
    -ms-overflow-style: scrollbar

*,
*::before,
*::after
    -webkit-box-sizing: inherit
    box-sizing: inherit

.container
    width: 100%
    padding-right: 15px
    padding-left: 15px
    margin-right: auto
    margin-left: auto

@media (min-width: 576px)
    .container
        max-width: 540px

@media (min-width: 768px)
    .container
        max-width: 720px

@media (min-width: 992px)
    .container
        max-width: 960px

@media (min-width: 1200px)
    .container
        max-width: 1140px

@media (min-width: 1500px)
    .container
        max-width: 1430px
